//
// Editor Grid
// --------------------------------------------------
.wp-block {
  max-width: 720px;

  /* Width of "wide" blocks */
  &[data-align="wide"] {
    max-width: 1080px;

    .wp-block {
      max-width: none;
    }
  }

  /* Width of "full-wide" blocks */
  &[data-align="full"] {
    max-width: none;

    .wp-block {
      max-width: none;
    }
  }

  .wp-block[data-align="wide"] {
    max-width: 968px;
  }

  .wp-block[data-align="full"] {
    max-width: none;
  }
}



